// 刷新页面数据

export default function (_this) {
    let params = {
      corrent: 999,
      skip: 0,
      username: _this.$store.state.username,
      newKey: _this.$store.state.newKey
    };
    _this.$store.dispatch("refreshdata", {
      http: _this.axios.post(
        "http://" + _this.$store.state.defaulthttp + "/read",
        params
      ),
    });
}