/* 
遗留问题
user 栏等用户数据库设计完在进行样式设定
更多的内容类型待数据库设计


 */


<template>
  <div class="writebox" @touchend="this.$store.commit('turnTopBar', 'write')" @touchmove.prevent>
    <div class="headbox">
      <div class="timeTag">
        <div>today:</div>
        {{ day() }}
      </div>
      <div class="typebox">
        <i>Type：</i>
        <input v-model="typeValue" />
        <div class="typeSelect">
          <div
            v-for="(value, index) in typeArrayWripper"
            :key="index"
            @click="turnTypeValue(value)"
          >
            {{ value }}
          </div>
        </div>
      </div>
    </div>
    <input type="text" class="head" placeholder="标题" v-model="contentHead">
    <textarea
      name="text"
      id="content"
      cols="30"
      rows="10"
      v-model="content"
      placeholder="在此处输入内容"
      class="content"
    ></textarea>
    <div id="sendbutton" @click="send">确认</div>
    <div id="usertap">
      <div>user</div>
      <div>contenttype</div>
    </div>
  </div>
</template>

<script>
import refresh from "@/api/refresh";

export default {
  naem: "write",
  data() {
    return {
      // 当前时间
      timenew: Array,
      // 当前内容
      content: "",
      // 当前类型
      typeValue: "问题",
      // 当前head
      contentHead: "",
      // 写入类型历史记录
      typeArrayWripper: ["问题", "日记", "日志", "心得", "总结"],
    };
  },
  methods: {
    // 获取当前时间并处理为所需内容
    day() {
      let arrayday = window.Date().split(" ");
      switch (arrayday[1]) {
        case "Jan":
          arrayday[1] = "1";
          break;
        case "Feb":
          arrayday[1] = "2";
          break;
        case "Mar":
          arrayday[1] = "3";
          break;
        case "Apr":
          arrayday[1] = "4";
          break;
        case "May":
          arrayday[1] = "5";
          break;
        case "Jun":
          arrayday[1] = "6";
          break;
        case "Jul":
          arrayday[1] = "7";
          break;
        case "Aug":
          arrayday[1] = "8";
          break;
        case "Sept":
          arrayday[1] = "9";
          break;
        case "Oct":
          arrayday[1] = "10";
          break;
        case "Nov":
          arrayday[1] = "11";
          break;
        case "Dec":
          arrayday[1] = "12";
          break;
        default:
          arrayday[1] = "未获取";
      }
      this.timenew = [arrayday[3], arrayday[1], arrayday[2]];
      return arrayday[3] + "-" + arrayday[1] + "-" + arrayday[2];
    },
    // 发送写入数据
    send() {
      this.$store.commit("turnStateM", {
        state: true,
        content: "正在写入",
        font: "fa fa-pulse fa-spinner",
      });
      let data = {
        writeday: this.timenew,
        type: this.typeValue,
        content: this.content,
        username: this.$store.state.username,
        newKey: this.$store.state.newKey,
        contentHead: this.contentHead,
      };
      this.axios
        .post("http://" + this.$store.state.defaulthttp + "/write", data)
        .then((res) => {
          if (res.data.state) {
            // 置空输入框
            this.contentHead = "";
            this.content = "";
            // 刷新read页面
            refresh(this);
            // 转到read页面
            this.$router.replace("/read");
            this.$store.commit("turnRouter", "read");
            // 关闭状态框
            let params = {
              state: true,
              content: "写入成功",
              font: "fa fa-check-square-o",
            };
            this.$store.commit("turnStateM", params);
            this.$store.dispatch("aysnturnStateM", false);
          }
        })
        .catch(() => {
          // 失败后传给状态框
          let params = {
            state: true,
            content: "写入失败",
            font: "fa fa-window-close",
          };
          this.$store.commit("turnStateM", params);
          this.$store.dispatch("aysnturnStateM", false);
        });
    },
    // 修改type栏
    turnTypeValue(value) {
      this.typeValue = value;
    },
  },
};
</script>

<style lang="less" scoped>
.writebox {
  box-sizing: border-box;
  width: 100vw;
  .headbox {
    display: flex;
    width: 100vw;
    height: 7vh;
    align-items: center;
    .timeTag {
      width: 50%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;
      align-content: initial;
      font-size: 20px;
      box-sizing: border-box;
      border-right: 1px solid rgb(145, 144, 144);
      color: cornflowerblue;
      div {
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-left: -85px;
        font-size: 15px;
      }
    }
    .typebox {
      width: 50%;
      display: flex;
      align-items: center;
      color: deepskyblue;
      input {
        height: 4vh;
        width: 30vw;
        font-size: 22px;
        color: tomato;
        border: none;
        text-align: center;
        &:focus {
          outline: none;
        }
      }
      input:focus + .typeSelect {
        max-height: 100%;
      }
      .typeSelect {
        background-color: white;
        position: fixed;
        top: 14vh;
        display: flex;
        flex-wrap: wrap;
        margin-left: 14vw;
        float: left;
        max-height: 0;
        overflow: hidden;
        transition: max-height 1s;
        box-shadow: 0 3px 3px rgb(187, 187, 187);
        width: 30vw;
        div {
          width: 30vw;
          height: 4vh;
          display: flex;
          align-content: center;
          justify-content: space-around;
          font-size: 22px;
        }
      }
    }
  }
  .head{
    width: 100vw;
    height: 4vh;
    border: none;
    font-size: 22px;
    padding: 10px;
    box-sizing: border-box;
    &:focus{
      outline: none;
    }
  }
  .content {
    width: 100vw;
    height: 45vh;
    border: none;
    box-sizing: border-box;
    padding: 20px;
    font-size: 22px;
    &:focus {
      outline: none;
    }
  }
  #sendbutton {
    background-color: rgb(96, 134, 156);
    height: 5vh;
    width: 24vw;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 20px 0 auto;
  }
  #usertap {
    display: flex;
    height: 8vh;
    div {
      display: flex;
      justify-content: center;
      align-items: center;
      &:nth-child(1) {
        width: 50%;
        box-sizing: border-box;
        border-right: rgb(170, 170, 170) 1px solid;
      }
      &:nth-child(2) {
        width: 50%;
      }
    }
  }
}
</style>